import React, { useState } from "react";
import PropTypes from "prop-types";
import BookModel from "../ModelBook";
import { nanoid } from "nanoid";

const BookAddForm = ({ onAdd }) => {
    const [form, setForm] = useState({name: ''}); 
    
    const handleChange = evt => {
        // const name = evt.target.name; ...
        // .      ,   
        const {name, value} = evt.target; 
        setForm(prevForm => ({...prevForm, [name]: value}));
        // console.log(evt.target.value) ... typing sim1, then + typing simb2 itc... -    
    }
    // console.log(form); ...    

    const handleSubmit = evt => {
        evt.preventDefault();                             //   DOM
        const book = new BookModel(nanoid(), form.name);  //  .   id    
        //   .   
        onAdd(book);                                      //    
        setForm({name: ''});                              //   
    }

    return ( 
        <form onSubmit={handleSubmit}>
            <input name="name" value={form.name} onChange={handleChange} />
        </form>
    );
}

BookAddForm.propTypes = {
    onAdd: PropTypes.func.isRequired
}

export default BookAddForm;
