import React from "react";
import PropTypes from "prop-types";
import ULSimple from "../ULSimple";

let ListItem = props =>
    <li className={props.className}>{props.children}</li>

/**
 *     -search 
 * 
 * @param {array} serchNav -        
 * @returns {JSX.Element} -  JSX-
 */
const Search = ({serchNav}) => {
    const bodyItem = serchNav.map(item => 
        <ListItem key={item} className="search-nav">{ item }</ListItem>);

    return (
        <div className="search">
            <div className="nav">
                <ul>{bodyItem}</ul>
            </div> 

            <div className="input">
                <a href="https://ya.ru"><span></span></a>
                <label htmlFor="">
                    <input />
                    <button></button>
                </label>
            </div>
            
            <div className="hint">
                <p> . , <span>  </span></p>
            </div>
        </div>
    );
}

Search.propTypes = {
    serchNav: PropTypes.array.isRequired
}

export default Search;